// UMD build entry point for json-diff-kit
// This file exposes the json-diff-kit functionality for use in vanilla JavaScript

import Differ from 'json-diff-kit/dist/differ.js';
import { Viewer } from 'json-diff-kit';
import React from 'react';
import ReactDOM from 'react-dom';

// Create a wrapper class for Viewer to make it constructable
class ViewerWrapper {
  constructor(props) {
    this.props = props;
    this.render = () => Viewer(props);
  }
}

// Export the main functionality
export { Differ, ViewerWrapper as Viewer, React, ReactDOM };

// For UMD compatibility, also attach to global scope
if (typeof window !== 'undefined') {
  window.JsonDiffKit = {
    Differ,
    Viewer: ViewerWrapper,
    React,
    ReactDOM
  };
}

// For CommonJS compatibility
if (typeof module !== 'undefined' && module.exports) {
  module.exports = {
    Differ,
    Viewer: ViewerWrapper,
    React,
    ReactDOM
  };
}
